<template>
  <shi-navbar :data-be-active-mod.sync="beActiveMod"></shi-navbar>
  <shi-login v-if="isActiveMod('shiLogin')"></shi-login>
  <shi-home v-if="isActiveMod('shiHome')"></shi-home>
</template>

<script>
  import Wilddog from 'Wilddog'
  import ShiNavbar from './components/ShiNavbar.vue'
  import ShiLogin from './components/ShiLogin.vue'
  import ShiHome from './components/ShiHome.vue'
  import { API_SHI_WILDDOG } from './consts'

  const shiWilddog = new Wilddog(API_SHI_WILDDOG)
  const Materialize = window.Materialize

  export default {
    vuex: {
      getters: {
        modBeActiveMod (state) {
          return state.mod.beActiveMod
        }
      },
      actions: {
        modToActiveMod ({dispatch, state}, mod) {
          dispatch('MOD_TO_ACTIVE_MOD', mod)
        },
        userToLogin ({dispatch, state}) {
          dispatch('USER_TO_LOGIN')
        }
      }
    },
    data () {
      return {
        beActiveMod: 'shiLogin'
      }
    },
    ready () {
      let self = this
      // 监控认证状态
      function authDataCallback (authData) {
        if (authData) {
          console.log('User ' + authData.uid + ' is logged in with ' + authData.provider)
          self.userToLogin()
          Materialize.toast('欢迎回来', 3000)
          self.modToActiveMod('shiHome')
        } else {
          console.log('User is logged out')
          Materialize.toast('需要登录', 3000)
          self.modToActiveMod('shiLogin')
        }
      }
      shiWilddog.onAuth(authDataCallback)
    },
    methods: {
      isActiveMod (mod) {
        return this.modBeActiveMod === mod ? 'active' : ''
      }
    },
    components: {
      ShiNavbar,
      ShiLogin,
      ShiHome
    }
  }
</script>

<style lang="less">
  @import (reference,less) "styles/variable.less";
  @import (css) "~materialize-css/dist/css/materialize.css";
  @import (less) "styles/material-icons.less";
  @import (less) "styles/base.less";
</style>



