<template>
  <div class="shi-login">
    <div class="row">
      <form class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input v-model="email" id="email" type="email" class="validate">
            <label for="email">邮件</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input v-model="password" id="password" type="password" class="validate">
            <label for="password">密码</label>
          </div>
        </div>
        <div class="row">
          <div class="col s12">
            <button @click="toLogin" class="btn waves-effect waves-light" type="button">登录
              <i class="material-icons right">send</i>
            </button>
            <button class="btn waves-effect waves-light" type="reset">重置</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
  import Wilddog from 'Wilddog'
  import { API_SHI_WILDDOG } from '../consts'

  const Materialize = window.Materialize
  const shiWilddog = new Wilddog(API_SHI_WILDDOG)

  export default {
    vuex: {
      getters: {
        userIsLogin (state) {
          return state.user.isLogin
        }
      },
      actions: {
        userToLogin ({dispatch, state}) {
          dispatch('USER_TO_LOGIN')
        },
        modToActiveMod ({dispatch, state}, mod) {
          dispatch('MOD_TO_ACTIVE_MOD', mod)
        }
      }
    },
    data () {
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      toLogin () {
        shiWilddog.authWithPassword({ email: this.email, password: this.password }, this._authHandler)
      },
      _authHandler (error, authData) {
        if (error) {
          console.log('Login Failed!', error)
          Materialize.toast('登录失败', 1000)
        } else {
          console.log('Authenticated successfully with payload:', authData)
          this.userToLogin()
          // let self = this
          // Materialize.toast('登录成功，即将跳转到首页', 1000, '', function () {
          //   self.modToActiveMod('shiHome')
          // })
        }
      }
    }
  }
</script>



