import 'materialize-css/dist/js/materialize.js'
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import store from './store'

Vue.use(Vuex)

/* eslint-disable no-new */
new Vue({
  el: 'body',
  components: { App },
  store: new Vuex.Store(store)
})



