<template>
  <div class="shi-navbar">
    <nav>
      <div class="nav-wrapper">
        <a href="javascript:;" class="brand-logo">湿湿娘</a>
        <a v-show="isHideMenu" href="javascript:;" data-activates="mobile-menu" class="button-collapse"><i class="material-icons">menu</i></a>
        <ul v-show="isHideMenu" class="right hide-on-med-and-down">
          <li :class="isActiveMod('shiHome')"><a href="javascript:;" @click="toActiveMod('shiHome')">主页</a></li>
          <li v-show="isHideLogin" :class="isActiveMod('shiLogin')"><a href="javascript:;" @click="toActiveMod('shiLogin')">登录</a></li>
          <li><a href="javascript:;" @click="toLogout()">退出</a></li>
        </ul>
        <ul v-show="isHideMenu" class="side-nav" id="mobile-menu">
          <li :class="isActiveMod('shiHome')"><a href="javascript:;" @click="toActiveMod('shiHome')">主页</a></li>
          <li v-show="isHideLogin" :class="isActiveMod('shiLogin')"><a href="javascript:;" @click="toActiveMod('shiLogin')">登录</a></li>
          <li><a href="javascript:;" @click="toLogout()">退出</a></li>
        </ul>
      </div>
    </nav>
  </div>
</template>
<script>
  import Wilddog from 'Wilddog'
  import { API_SHI_WILDDOG } from '../consts'

  const $ = window.$
  // const Materialize = window.Materialize
  const shiWilddog = new Wilddog(API_SHI_WILDDOG)

  export default {
    vuex: {
      getters: {
        modBeActiveMod (state) {
          return state.mod.beActiveMod
        },
        userIsLogin (state) {
          return state.user.isLogin
        }
      },
      actions: {
        modToActiveMod ({dispatch, state}, mod) {
          dispatch('MOD_TO_ACTIVE_MOD', mod)
        },
        userToLogout ({dispatch, state}) {
          shiWilddog.unauth()
          dispatch('USER_TO_LOGOUT')
        }
      }
    },
    computed: {
      isHideMenu () {
        return !(this.modBeActiveMod === 'shiLogin')
      },
      isHideLogin () {
        return !(this.userIsLogin)
      }
    },
    methods: {
      isActiveMod (mod) {
        return this.modBeActiveMod === mod ? 'active' : ''
      },
      toActiveMod (mod) {
        this.modToActiveMod(mod)
      },
      toLogout () {
        this.userToLogout()
        // let self = this
        // Materialize.toast('退出成功，即将跳转到登录页', 1000, '', function () {
        //   self.modToActiveMod('shiLogin')
        // })
      }
    },
    ready () {
      $(function () {
        $('.button-collapse').sideNav({
          closeOnClick: true
        })
      })
    }
  }
</script>



