const state = {
  user: {
    isLogin: false
  },
  mod: {
    beActiveMod: 'shiLogin'
  }
}

const mutations = {
  USER_TO_LOGIN (state) {
    state.user.isLogin = true
  },
  USER_TO_LOGOUT (state) {
    state.user.isLogin = false
  },
  MOD_TO_ACTIVE_MOD (state, mod) {
    state.mod.beActiveMod = mod
  }
}

const store = {
  state,
  mutations
}

export default store



